import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { removeSpaces, validarEmail } from "../helpers/helper";
// import { deleteUserDB } from "../redux/actions/users_actions"
import { useToasts } from "react-toast-notifications";
import {
  changeCurrentUser,
  cleanListSearch,
  clearUserSearch,
  consultarUsuario,
} from "../redux/actions/users_actions";
import { createUser } from "../redux/actions/auth_actions";

export const useFormBuscarUsuario = ({ initialState = {} }) => {
  const [values, setValues] = useState(initialState);
  const [salve, setSalve] = useState(false);
  const {
    users: { fetching },
  } = useSelector((state) => state);
  const [findValues, setFindValues] = useState({
    campo: "email",
    campoValue: "",
  });

  const dispatch = useDispatch();
  const { addToast } = useToasts();

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 2500,
    });
  };

  const handleInputChange = ({ target }) => {
    setValues({
      ...values,
      [target.name]: target.value,
    });
  };

  const handlerBlurEmail = () => {
    if (validarEmail(values.email)) {
      setValues({
        ...values,
        errorEmail: "",
      });
    } else {
      setValues({
        ...values,
        errorEmail: "El email no es válido",
      });
    }
  };

  const eliminarUsuario = (e, id, email) => {
    e.preventDefault();
    //  //console.log(id);
    //  //console.log(email);
    // dispatch(deleteUserDB(id, email))
  };

  const buscarUsuario = (e) => {
    if (e !== undefined) {
      e.preventDefault();
    }
    if (
      findValues.campo === "" ||
      findValues.campo === undefined ||
      findValues.campoValue === "" ||
      findValues.campoValue === undefined
    ) {
      toastSms("Debes ingresar un valor de busqueda", "error");
    } else {
      if (findValues.campo === "email") {
        if (removeSpaces(findValues.campoValue).includes("@")) {
          toastSms(
            "por favor no ingrese @rapifas.com, solo nombre de usuario",
            "info"
          );
        } else {
          dispatch(
            consultarUsuario(
              findValues.campo.trim(),
              `${findValues.campoValue}@rapifas.com`,
              toastSms
            )
          );
        }
      } else {
        dispatch(
          consultarUsuario(
            findValues.campo.trim(),
            findValues.campoValue.trim().toUpperCase(),
            toastSms
          )
        );
      }
    }
  };

  const handlerChangeUser = (user) => {
    dispatch(changeCurrentUser(user));
  };

  const editUser = () => {
    changeModal(false);

    if (
      (values.rol !== "" && values.responsable !== "",
      values.email !== "",
      values.codigoPostal !== "",
      values.ruc !== "",
      values.telefonoEmpresa !== "",
      values.telefonoResponsable !== "",
      values.direccion !== "",
      values.provincia !== "",
      values.canton !== "",
      values.parroquia !== "")
    ) {
      // HACER DISPATCH
      let dataUser = {
        rol: values.rol,
        razonSocial: values.razonSocial.toUpperCase(),
        ruc: values.ruc,
        direccion: values.direccion,
        referencia: values.referencia,
        provincia: values.provincia.toUpperCase(),
        canton: values.canton.toUpperCase(),
        parroquia: values.parroquia.toUpperCase(),
        responsable: values.responsable.toUpperCase(),
        telefonoEmpresa: values.telefonoEmpresa,
        telefonoResponsable: values.telefonoResponsable,
        codigoPostal: values.codigoPostal,
        email: `${values.email}@rapifas.com`,
        id: values.id,
      };
      const msgCreateOk = () =>
        toastSms("Datos de usuario actualizados", "success");
      const msgCreateError = (error) => toastSms(`${error}`, "error");
      dispatch(createUser(dataUser, "", "", msgCreateOk, msgCreateError, true));
      dispatch(cleanListSearch());
      dispatch(clearUserSearch());
      setValues({});
    } else {
      setValues({
        ...values,
        errorMsg: "Existen Errores en el formulario",
      });
      toastSms(values.errorMsg, "error");
    }
  };
  const handlerInputFind = ({ target }) => {
    //  //console.log("HANDLE INPUT FIND")
    setFindValues({
      ...findValues,
      [target.name]: target.value,
    });
  };
  const changeModal = (status) => {
    return setSalve(status);
  };

  return [
    values,
    findValues,
    salve,
    changeModal,
    handleInputChange,
    editUser,
    handlerBlurEmail,
    handlerInputFind,
    buscarUsuario,
    handlerChangeUser,
    setValues,
    eliminarUsuario,
  ];
};
