import {
  LOGIN,
  LOGIN_ERROR,
  LOGIN_SUCCESS,
  REGISTER_USER,
  REGISTER_USER_ERROR,
  REGISTER_USER_SUCCESS,
  CERRAR_SESION,
  GET_ROL_FUNCTIONS,
  GET_ROL_FUNCTIONS_ERROR,
  GET_ROL_FUNCTIONS_SUCCESS,
  UPDATE_USER,
} from "../../types/types";

//  //console.log(localStorage.getItem("dataProfile"));

const initialState = {
  fetching: false,
  accessToken:
    localStorage.getItem("accessToken") &&
    localStorage.getItem("accessToken") != "undefined"
      ? localStorage.getItem("accessToken")
      : null,
  errorLogin: null,
  dataProfile:
    localStorage.getItem("dataProfile") &&
    localStorage.getItem("dataProfile") != "undefined"
      ? JSON.parse(localStorage.getItem("dataProfile"))
      : null,
  funciones:
    localStorage.getItem("rfas_funciones") &&
    localStorage.getItem("rfas_funciones") != "undefined"
      ? JSON.parse(localStorage.getItem("rfas_funciones"))
      : null,
  error: null,
};

export const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case LOGIN:
    case REGISTER_USER:
      return {
        ...state,
        fetching: true,
        errorLogin: null,
      };
    case REGISTER_USER_SUCCESS:
      return {
        ...state,
        fetching: false,
      };
    case REGISTER_USER_ERROR:
      return {
        ...state,
        fetching: false,
        errorLogin: action.payload,
      };
    case LOGIN_SUCCESS:
      return {
        ...state,
        fetching: false,
        accessToken: action.payload.accessToken,
        dataProfile: action.payload.dataProfile,
        errorLogin: null,
        funciones: action.payload.funciones,
      };
    case LOGIN_ERROR:
      return {
        ...state,
        fetching: false,
        accessToken: null,
        dataProfile: null,
        errorLogin: action.payload,
      };
    case CERRAR_SESION:
      return {
        ...state,
        fetching: false,
        accessToken: null,
        errorLogin: null,
        dataProfile: null,
      };
    case GET_ROL_FUNCTIONS:
      return state;
    case GET_ROL_FUNCTIONS_SUCCESS:
      return {
        ...state,
        funciones: action.payload,
      };
    case GET_ROL_FUNCTIONS_ERROR:
      return {
        ...state,
        error: action.payload,
      };
    case UPDATE_USER:
      return {
        ...state,
        fetching: action.payload,
      };
    default:
      return state;
  }
};
