import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import {
  namesCantones,
  namesParroquias,
  namesProvincias,
} from "../../../../constants/helper_provincias";
import { listProvincias } from "../../../../constants/new_provincia";
import { useFormBuscarUsuario } from "../../../../hooks/use_form_buscarUsuario";
import Dashfooter from "../../../dash-componets/footter/dashfooter";
import Spinner from "../../../spinner/spinner";
import "../buscar_usuario/buscar_usuarios.css";

function BuscarUsuarios() {
  const {
    users: { currentUser, fetching, listUsers },
  } = useSelector((state) => state);
  const [
    values,
    findValues,
    salve,
    changeModal,
    handleInputChange,
    editUser,
    handlerBlurEmail,
    handlerInputFind,
    buscarUsuario,
    handlerChangeUser,
    setValues,
    eliminarUsuario,
  ] = useFormBuscarUsuario(currentUser);
  const { campo, campoValue } = findValues;
  const {
    rol,
    razonSocial,
    ruc,
    direccion,
    referencia,
    provincia,
    canton,
    parroquia,
    responsable,
    telefonoEmpresa,
    telefonoResponsable,
    codigoPostal,
    idRemitente,
    email,
    errorMsg,
    errorEmail,
  } = values;

  useEffect(() => {
    // //  //console.log("Dentro de useEffect BUSCAR USUARIO")
    if (currentUser.nombre !== "") {
      setValues(currentUser);
    }
  }, [currentUser, setValues]);
  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4">
      <div className="container-fluid pt-4">
        <h4 className="title-separtor">Búsqueda de Usuarios</h4>
        <hr />
        <div className="card card-body">
          <div className="row align-items-center">
            <div className="col-sm-12 col-md-2">
              <b className="titleSection-form">Buscar por:</b>
            </div>
            <div className="col-sm-12 col-md-3">
              <select
                className="form-select"
                name="campo"
                value={campo}
                onChange={handlerInputFind}
              >
                <option value="email">Usuario</option>
                <option value="responsable">Nombre</option>
                <option value="ruc">Cedula/DNI/RUC</option>
              </select>
            </div>

            <form className="col-sm-12 col-md-7" onSubmit={buscarUsuario}>
              <div className="input-group">
                <input
                  type="text"
                  className="form-control dropdown-toggle"
                  placeholder="Buscar ..."
                  name="campoValue"
                  value={campoValue}
                  onChange={handlerInputFind}
                />
                <span className="mdi mdi-magnify search-icon" />
                {findValues?.campo === "email" ? (
                  <div class="input-group-append">
                    <span
                      class="input-group-text size-rapifas"
                      id="basic-addon2"
                    >
                      @rapifas.com
                    </span>
                  </div>
                ) : (
                  <></>
                )}
                {fetching ? (
                  <Spinner />
                ) : (
                  <button
                    className="btn-solid-secondary px-4 "
                    type="button"
                    onClick={buscarUsuario}
                  >
                    Buscar
                  </button>
                )}
              </div>
            </form>
          </div>
        </div>
        <div className="card card-body">
          <h5 className="titleSection-form">Usuarios</h5>
          <hr />
          <div className="table-responsive">
            <table className="table nowrap">
              <thead>
                <tr className="header-table">
                  <th
                    className="text-nowrap "
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title="ID"
                  >
                    ID
                  </th>
                  <th
                    className="text-nowrap "
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title="Nombre"
                  >
                    Nombre
                  </th>
                  <th
                    className="text-nowrap "
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title="Email"
                  >
                    Email
                  </th>
                  <th
                    className="text-nowrap "
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title="Rol"
                  >
                    Rol
                  </th>
                  <th
                    className="text-nowrap "
                    data-bs-toggle="tooltip"
                    data-bs-placement="bottom"
                    title="Acciones"
                  >
                    Acciones
                  </th>
                </tr>
              </thead>

              <tbody>
                {listUsers.map((user, index) => (
                  <tr key={user.id} className="hover-table">
                    <td
                      className="text-nowrap"
                      data-toggle="tooltip"
                      data-placement="top"
                      title={user.id}
                    >
                      {index + 1}
                    </td>
                    <td
                      className="text-truncate text-nowrap"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title={(user.responsable ?? "").toUpperCase()}
                    >
                      {(user.responsable ?? "").toUpperCase()}
                    </td>
                    <td
                      className="text-truncate text-nowrap"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title={user.email ?? "---"}
                    >
                      {user.email ?? "---"}
                    </td>
                    <td
                      className="text-truncate text-nowrap"
                      data-bs-toggle="tooltip"
                      data-bs-placement="bottom"
                      title={user.rol ?? "---"}
                    >
                      {user.rol ?? "---"}
                    </td>
                    <td>
                      {/* <a
                        className="actions anulacion"
                        type="button"
                        data-toggle="tooltip"
                        data-placement="top"
                        title="Anular"
                        data-bs-toggle="modal"
                        data-bs-target="#anularguia"
                        onClickCapture={(e) =>
                          eliminarUsuario(e, user.id, user.email)
                        }
                      >
                        <i className="material-icons">delete_forever</i>
                      </a> */}
                      <a
                        className="actions view"
                        type="button"
                        data-toggle="tooltip"
                        data-placement="top"
                        title="Ver Detalles"
                        onClickCapture={() => handlerChangeUser(user)}
                      >
                        <i className="material-icons">remove_red_eye</i>
                      </a>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
            {listUsers.length === 0 ? (
              <div className="no-Item">
                <p className="text-muted noGuias">
                  No hay usuarios en la lista
                </p>
              </div>
            ) : (
              <></>
            )}
          </div>
        </div>
        {currentUser.email === "" ? (
          <></>
        ) : (
          <div>
            <div className="col-sm-12 col-md-6 align-self-start">
              <h4 className="title-separtor pt-3">Detalles Usuario</h4>
            </div>
            <hr />
            <div className="card card-body">
              {/* <div className='no-Item'>
						<p className="text-muted noGuias">No hay guia seleccionada</p>
					</div> */}
              <h5 className="titleSection-form">Datos de Usuario</h5>
              <hr />
              <div className="row ">
                <div className="col-lg-6 ">
                  <form>
                    <div className="mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Responsable
                      </label>
                      <input
                        type="text"
                        //
                        className="form-control"
                        placeholder="Nombres"
                        value={responsable}
                        name="responsable"
                        onChange={handleInputChange}
                      />
                    </div>
                    <div className="mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Razón Social
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        placeholder="Nombre de la Empresa, Institución o Local."
                        name="razonSocial"
                        value={razonSocial}
                        onChange={handleInputChange}
                      />
                    </div>

                    <div className="mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Dirección
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        placeholder="Calle principal, e intersección (maximo 70 caracateres)"
                        value={direccion}
                        name="direccion"
                        onChange={handleInputChange}
                      />
                    </div>
                    <div className="mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Referencia
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        placeholder="Adjunte número de casa o descripción breve del destino"
                        value={referencia}
                        name="referencia"
                        onChange={handleInputChange}
                      />
                    </div>
                    <div className="mb-3">
                      <label className="form-label-input">Provincia *</label>
                      <select
                        autoComplete="off"
                        className={`form-select`}
                        value={provincia}
                        name="provincia"
                        onChange={handleInputChange}
                        required
                      >
                        <option key={`#1_remitente`} value="">
                          Seleccionar la Provincia
                        </option>
                        {namesProvincias(listProvincias).map((e) => (
                          <option key={`${e}_remitente`} value={e}>
                            {e}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="mb-3">
                      <label className="form-label-input">Cantón *</label>
                      <select
                        className="form-select"
                        value={canton}
                        name="canton"
                        onChange={handleInputChange}
                      >
                        {/* <option key={`#2_remitente`} value={canton === "" || canton ===undefined?   "Seleccionar el Cantón" : canton }>
                        { canton === "" || canton ===undefined?   "Seleccionar el Cantón" : canton }
                        </option> */}

                        {namesCantones(provincia?.trim()).map((e) => {
                          return (
                            <option key={`${e}_canton_remitente`} value={e}>
                              {e}
                            </option>
                          );
                        })}
                      </select>
                    </div>

                    <div className="mb-3">
                      <label className="form-label-input">Parroquia *</label>
                      <select
                        className="form-control"
                        value={parroquia}
                        name="parroquia"
                        onChange={handleInputChange}
                      >
                        {namesParroquias(canton).map((e) => {
                          return (
                            <option key={`${e}_parroquia_remitente`} value={e}>
                              {e}
                            </option>
                          );
                        })}
                      </select>
                    </div>
                  </form>
                </div>
                <div className="col-lg-6 ">
                  <form>
                    <div className="mb-3">
                      <div className="mb-3">
                        <label
                          htmlFor="simpleinput"
                          className="form-label-input"
                        >
                          Ruc/Cédula
                        </label>
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Ruc / Cédula / DNI"
                          value={ruc}
                          name="ruc"
                          onChange={handleInputChange}
                        />
                      </div>
                      <label htmlFor="simpleinput" className="form-label-input">
                        Email
                      </label>
                      <input
                        type="email"
                        className="form-control"
                        id="inputEmail"
                        placeholder="example@example.com"
                        value={email}
                        name="email"
                        disabled={true}
                      />
                      {errorEmail && errorEmail !== "" ? (
                        <div className="container-error">
                          <p className="text-error">{errorEmail}</p>
                        </div>
                      ) : (
                        <></>
                      )}
                    </div>
                    <div className="mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Código Postal
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        placeholder="12514"
                        value={codigoPostal}
                        name="codigoPostal"
                        onChange={handleInputChange}
                      />
                    </div>

                    <div className="mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Télefono Empresa
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        placeholder="Apellidos Completos"
                        value={telefonoEmpresa}
                        name="telefonoEmpresa"
                        onChange={handleInputChange}
                      />
                    </div>
                    <div className="mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Teléfono Responsable
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        placeholder="Apellidos Completos"
                        value={telefonoResponsable}
                        name="telefonoResponsable"
                        onChange={handleInputChange}
                      />
                    </div>

                    <div className="mb-3">
                      <label htmlFor="simpleinput" className="form-label-input">
                        Rol
                      </label>
                      <select
                        className="col  align-self-start form-select"
                        placeholder="Tipo Usuario"
                        value={rol}
                        onChange={handleInputChange}
                        name="rol"
                      >
                        <option value="admin">Administrador</option>
                        <option value="puntoVenta">Puntos de Venta</option>
                        <option value="sucursal">Sucursales</option>
                        <option value="Corporativo">
                          Clientes Coorporativos
                        </option>
                        <option value="cartero">Cartero</option>
                      </select>
                    </div>
                  </form>
                </div>
                {errorMsg && errorMsg !== "" ? (
                  <div className="container-error">
                    <p className="text-error">{errorEmail}</p>
                  </div>
                ) : (
                  <></>
                )}
              </div>
              <hr />
              <div className="row">
                <div className="col mx-1 p-2 bt-a"></div>
                <button
                  type="button"
                  className="btn btn-success col mx-1 bt-a p-2"
                  data-bs-toggle="modal"
                  data-bs-target="#anularguia"
                  onClick={() => changeModal(true)}
                >
                  {fetching === true ? (
                    <div class="spinner-border spinner-border-sm" role="status">
                      <span class="sr-only">Loading...</span>
                    </div>
                  ) : (
                    "Guardar Cambios"
                  )}
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
      <div
        className="modal fade"
        id="anularguia"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="staticBackdropLabel">
                {salve === true
                  ? "¿Está seguro de guardar los cambios?"
                  : "Eliminar Usuario"}
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>

            <div className="modal-body">
              <p>
                {salve === true
                  ? "Los cambios serán aplicados, y los datos anteriores ya no podrá recuperarse"
                  : "Esta seguro de eliminar este usuario, una vez eliminado no se podra recuperar sus datos."}
              </p>
            </div>

            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
                // onClick={() => {
                //   showDetails(false, null);
                // }}
              >
                Cancelar
              </button>
              {salve === true ? (
                <button
                  type="submit"
                  className=" btn btn-success p-2"
                  data-bs-dismiss="modal"
                  onClick={editUser}
                >
                  Actualizar Usuario
                </button>
              ) : (
                <button
                  type="submit"
                  className=" btn btn-danger p-2"
                  data-bs-dismiss="modal"
                >
                  Eliminar Usuario
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
      <Dashfooter />
    </div>
  );
}

export default BuscarUsuarios;
